<template>
  <div class="wrapper">
    <img class="wrapper__img" src="http://www.dell-lee.com/imgs/vue3/user.png" alt="">
    <input class="wrapper__input" type="text" placeholder="请输入用户名" v-model="username">
    <input class="wrapper__input" type="password" placeholder="请输入密码" v-model="password">
    <input class="wrapper__input" type="password" placeholder="确认密码" v-model="confirm">
    <div class="wrapper__register" @click="handleRegister">注册</div>
    <div class="wrapper__link">
      <span @click="handleToLoginClick">已有账号去登陆</span>
    </div>
  </div>
  <Toast v-if="toastInfo.show" :message="toastInfo.message"/>
</template>

<script>
import { reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'
import { post } from '@/utils/request'
import Toast, { useToastEffect } from '@/components/Toast.vue'

export default {
  name: 'RegisterView',
  components: { Toast },
  setup () {
    const router = useRouter()
    const registerInfo = reactive({
      username: '',
      password: '',
      confirm: ''
    })
    const {
      toastInfo,
      showToast
    } = useToastEffect()
    const handleRegister = async () => {
      if (!registerInfo.username) return showToast('用户名不能为空')
      if (!registerInfo.password) return showToast('密码不能为空')
      if (!registerInfo.confirm) return showToast('请确认密码')
      if (registerInfo.password !== registerInfo.confirm) return showToast('密码不一致')
      try {
        const result = await post('/api/user/register', {
          username: registerInfo.username,
          password: registerInfo.password
        })
        if (result?.errno === 0) {
          router.push('/login')
        } else {
          showToast('注册失败')
        }
      } catch (e) {
        showToast('请求失败')
      }
    }
    const handleToLoginClick = () => {
      router.push('/login')
    }
    return {
      ...toRefs(registerInfo),
      toastInfo,
      handleRegister,
      handleToLoginClick
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";

.wrapper {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  padding: 0 .4rem;

  &__img {
    display: block;
    width: .66rem;
    height: .66rem;
    margin: 0 auto .4rem;
  }

  &__input {
    width: 100%;
    height: .48rem;
    margin-bottom: .16rem;
    background: #F9F9F9;
    border: .01rem solid rgba(0, 0, 0, 0.10);
    border-radius: .06rem;
    outline: none;
    font-size: .16rem;
    padding: 0 .16rem;
    box-sizing: border-box;

    &::placeholder {
      color: $content-notice-fontcolor;
    }
  }

  &__register {
    line-height: .48rem;
    background: #0091FF;
    box-shadow: 0 .04rem .08rem 0 rgba(0, 145, 255, 0.32);
    border-radius: .04rem;
    font-size: .16rem;
    color: #FFFFFF;
    text-align: center;
    margin: .16rem 0;
  }

  &__link {
    line-height: .2rem;
    text-align: center;
    font-size: .14rem;
    color: $content-notice-fontcolor;
  }
}
</style>
